import { ActivatedRoute } from "@angular/router";
import { EnvService } from "src/app/services/env.service";
import {
  SessionsManagerService,
  ChatType,
} from "src/app/services/sessions-manager-service";
import { IonRefresher } from "@ionic/angular";
import { User, RoleId } from "src/app/models/user";
import { Component, OnInit } from "@angular/core";
import { UntilDestroy, untilDestroyed } from "@ngneat/until-destroy";

@UntilDestroy()
@Component({
  selector: "app-peer-chats",
  templateUrl: "./peer-chats.page.html",
  styleUrls: ["./peer-chats.page.scss"],
})
export class PeerChatsPage implements OnInit {
  peers: User[] = [];
  refresher: IonRefresher = null;
  getName = User.getName;
  getRoleName = User.getRoleName;
  defaultBackUrl = "";
  roleId = null;
  constructor(
    private sessionsManager: SessionsManagerService,
    public env: EnvService,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.roleId = this.route.snapshot.params.roleId;
    if (this.roleId == this.env.USER_ROLE_ID) {
      this.defaultBackUrl = "usr-reservations";
    } else {
      this.defaultBackUrl = "pvd-reservations";
    }
    this.sessionsManager.myPeerSubscriptionsShared
      .getUpdateNotifier(this)
      .subscribe((result) => {
        this.peers = this.sessionsManager.myPeerSubscriptionsShared.resource();
        if (this.refresher) {
          this.refresher.complete();
        }
      });
    this.sessionsManager.myPeerSubscriptionsShared.request();
  }

  refresh(ev) {
    this.refresher = ev.target;
    this.sessionsManager.myPeerSubscriptionsShared.request();
  }
  chat(user) {
     this.sessionsManager.startPeer2PeerChat(user);
    let type: ChatType;
    if (this.roleId == RoleId.USER_ROLE_ID) {
      type = ChatType.P2P_WITH_PROVIDER;
    } else {
      type = ChatType.P2P_WITH_USER;
    }
    //this.sessionsManager.enterChat(type, user.id,this.roleId == RoleId.PROVIDER_ROLE_ID);
  }
  getUnreadMessages(user: User) {
    return this.sessionsManager.getUserUnreadMessagesCount(user);
  }
  isOnline(user: User) {
    return this.sessionsManager.isUserOnline(user);
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
  }
}
